export default {
  install: function(Vue) {
    Vue.prototype.port = {
      'basicsDeta': '/member/memberdetail', //    get
      'goodsList': '/shop/goods-list', //   get
      'goodsDeta': '/shop/goods-details', //   get
      'buyT': '/shop/buy', // get
      'noticeList': '/announcement/index', // get
      'noticeDeta': '/announcement/select', // get
      'hasGCoin': '/withdrawals/coin', // get idid
      'requestGCoin': '/withdrawals/add', // get   ididcoin
      'hervestList': '/withdrawals/index', // get idid
      'memberCoin': '/member/coin', //  get
      'sendGive': '/member/give', // post  give_member_id(id) give_coin()
      'giveRecord': '/member/gives', // GET
      'getRecord': '/member/gain', // GET
      'getIndexBanner': '/announcement/list', //banner get
      'bannerDeta': '/announcement/listid', //banner get
      'changeBasics': '/member/update', // POST name deposit_bank bank_account address
      'changePsw': '/member/pass', // post password() newpassword repassword
      'myEarnings': '/member/bonus', // get type:0 1 2
      'myFruiter': '/member/fruiter', // get
      'login': '/site/login', // post(id,2) (111111)
      'logout': '/site/logout', //  get
      'suerNextId': '/member/able', //
      'sureId': '/member/validate', // get
      'register': '/member/register', // POST
      'begats': '/member/tree', // post
      'authCode': '/site/captcha?refresh', // get
      'adminTree': '/member/full-tree', // 
      'call110': '/member/outline', //
      'accountDeta': '/member/relate', //  get 
      'statisticsLog': '/member/log', //  type 12
    };
    Vue.prototype.hostUrl = 'http://api.wantu3.cn'; // 
    //Vue.prototype.hostUrl = 'http://api.wantu3.ypxl/'; //
    Vue.prototype.ajax = function(url, data, methodType, callback) {
      var _this = this;
      //const  HostUrl='http://192.168.2.174/';
      this.isLoading = true;
      this.iSsuccess = true;
      $.ajax({
        type: methodType,
        url: _this.hostUrl + url,
        data: data,
        timeout: 15000,
        dataType: 'json',
        xhrFields: {
          withCredentials: true
        },
        success: function(data) {
          _this.isLoading = false;
          _this.iSsuccess = false;
          if (data.code == 10001) {
            let timeStr = data.data.offTime;
            _this.$alert('' + timeStr, '', {
              confirmButtonText: '',
              type: 'error',
              callback: action => {
                window.location.href = 'http://www.wantu3.cn/#/login'
              }
            });
            return
          } else {
            callback(data);
          }
          //   var sDate = new Date(parseInt(data.timestamp) * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ');
          //   // var clock = rStr.slice((rStr.indexOf('shoper_id=')+10),(rStr.indexOf('&')));
          //   var clock = sDate.slice(sDate.indexOf(' ') + 1)
          //   var TimeP = clock.split(":")
          //   if (TimeP[0] < 8) {
          //       alert('0000800');
          //     return
          //   } else {
          //     callback(data);
          //   }
        },
        error: function(XMLHttpRequest, textStatus, errorThrown) {
          // console.log(textStatus)
          if (textStatus == 'timeout') {
            //alert('');
            _this.$message({
              message: '~!~',
              type: 'warning'
            });
            _this.isLoading = false;
            _this.iSsuccess = false;
          } else if (textStatus == 'error') {
            //alert('');
            _this.$message({
              message: '~!~',
              type: 'warning'
            });
            _this.isLoading = false;
            _this.iSsuccess = false;
          } else if (textStatus == 'parsererror') {
            //alert('');
            _this.$message({
              message: '~!~',
              type: 'warning'
            });
            _this.isLoading = false;
            _this.iSsuccess = false;
          }
        }
      });
    };

    // (function () {
    //   const  HostUrl='http://192.168.2.222:8080/';
    //   $.ajax({
    //     type: 'get',
    //     url: HostUrl+'role/get-roles',
    //     data:'',
    //     dataType:'json',
    //     success: function (data) {
    //       console.log(data.data)
    //       window.localStorage.setItem('roledata',JSON.stringify(data.data));
    //     },
    //     error:function (err) {
    //       console.log(err);
    //     }
    //   });
    // })()


  }
}



