<!--
    =============  =============
-->
<template>
<div id="center">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>
  <!--  -->
  <basicsOverview :teamFlag="true"></basicsOverview>

  <!--  -->
  <div class="centerMyBasic global_boxSad">
    <div class="centerTitBox">
      <div class="global_flexC">
        <span class="global_titLog"></span> 
      </div>
      <div class="CMyBasicBtnBox">
          <!--  -->
        <button type="button" name="button" v-show="!changeBasicsBtnFlag" class="global_orgBtn" @click="changeBlock('c')"></button>
        <button type="button" name="button" v-show="!basicsBtnFlag" class="global_grayBtn" @click="changeBlock('b')"></button>
        <button type="button" name="button" v-show="!passwordBtnFlag" class="global_orgBtn" @click="changeBlock('p')"></button>
        <button type="button" name="button" v-show="passwordBtnFlag" class="global_grayBtn" @click="changeBlock('b')"></button>
      </div>
    </div>
    <div class="centerMainBox">
        <!--  -->
      <transition name="el-zoom-in-center">
        <basics v-if="basicsFlag" class="transition-box"></basics>
      </transition>
      <transition name="el-zoom-in-center">
        <password v-if="passwordFlag" class="transition-box"></password>
      </transition>
      <transition name="el-zoom-in-bottom">
        <changeBasics v-if="changeBasicsFlag" class="transition-box"></changeBasics>
      </transition>
    </div>
  </div>

  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
import basics from '@/components/center/basics/basics.vue';
import password from '@/components/center/password/password.vue';
import changeBasics from '@/components/center/changeBasics/changeBasics.vue';
import basicsOverview from '@/components/basicsOverview/basicsOverview.vue';

export default {
  name: "center",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      basicsFlag: true, //TRUE
      passwordFlag: false, //
      changeBasicsFlag: false, //
      basicsBtnFlag: true, //TRUE
      passwordBtnFlag: false, //
      changeBasicsBtnFlag: false, //
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex' //
    }
  },
  beforeRouteEnter(to, from, next) {
    // console.log(sessionStorage.getItem('accessToken'));
    let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next()
    }
    // next(vm => {
       //  `vm` 
       // console.log('beforeRouteEnter');
    // })
  },
  components: {
    'basics': basics,
    'password': password,
    'changeBasics': changeBasics,
    'basicsOverview': basicsOverview,
  },
  methods: {
      //
    changeBlock: function changeBlock(name) {
      var tempTimeOut
      var _this = this;
      this.iSsuccess = true;
      this.isLoading = true;
      clearTimeout(tempTimeOut)
      if (name == 'b') {
        _this.passwordFlag = false;
        _this.changeBasicsFlag = false;
        _this.passwordBtnFlag = false;
        _this.changeBasicsBtnFlag = false;
        _this.basicsBtnFlag = true;
        tempTimeOut = setTimeout(function() {
          _this.basicsFlag = true;
          _this.iSsuccess = false;
          _this.isLoading = false;
        }, 260)
      } else if (name == 'p') {
        _this.basicsFlag = false;
        _this.changeBasicsFlag = false;

        _this.passwordBtnFlag = true;
        _this.changeBasicsBtnFlag = false;
        _this.basicsBtnFlag = false;
        tempTimeOut = setTimeout(function() {
          _this.passwordFlag = true;
          _this.iSsuccess = false;
          _this.isLoading = false;
        }, 260)
      } else if (name == 'c') {
        _this.passwordFlag = false;
        _this.basicsFlag = false;

        _this.passwordBtnFlag = false;
        _this.changeBasicsBtnFlag = true;
        _this.basicsBtnFlag = false;
        tempTimeOut = setTimeout(function() {
          _this.changeBasicsFlag = true;
          _this.iSsuccess = false;
          _this.isLoading = false;
        }, 260)
      }
    },
  }
}
</script>
<style>
.transition-box {
  width: 100%;
  background-color: #fff5ee;
}

.centerMainBox {
  min-height: 3.05rem;
}
</style>



