<!--
    =============  =============
-->
<template>
<div id="giveToFriend">
  <div class="bodyMainHead">
    <p>
      
      <span>{{GFruit}}</span>
    </p>
    <p>
      
      <span>{{GSeed}}</span>
    </p>
  </div>
  <div class="harvestRequestIptBox global_boxSad">
    <div class="bodyMainRow">
      <span></span>
      <div class="radioBox">
        <el-radio class="radio" v-model="radio" label="1"></el-radio>
        <el-radio class="radio" v-model="radio" label="2"></el-radio>
      </div>
    </div>
    <div class="bodyMainRow">
      <span> </span>
      <div class="shareSureBox" style="margin-right:0;">
        <input type="text" name="" :value="giveForId" v-model="giveForId" class="global_IptSad harvestRequestIpt" placeholder="">
        <div class="global_boxSad shareSureBtn" @click="suerTheId()">
          
        </div>
      </div>
    </div>
    <p class="bodyMainRow">
      <span></span>
      <input type="number" name="" :value="giveCoin" v-model="giveCoin" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
  </div>
  <div class="goTo110" @click="sureSend()">
    
  </div>


  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "giveToFriend",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      radio: '1', //   1
      GFruit: '...', //
      GSeed: '...', //
      giveForId: '', //ID
      giveCoin: '', //
    }
  },
  methods: {
    //  ID
    suerTheId: function suerTheId() {
      var _this = this;
      this.ajax(_this.port.sureId, {
        username: _this.giveForId
      }, 'GET', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.$alert('ID' + res.data.name, '', {
            confirmButtonText: '',
            callback: action => {}
          });
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
    },
    //
    getUserCoin: function getUserCoin() {
      var _this = this;
      this.ajax(_this.port.memberCoin, {}, 'GET', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.GFruit = res.data[0].a_coin;
          _this.GSeed = res.data[0].b_coin;
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
    },
    //
    // sendAjaxFun
    sureSend: function sureSend() {
      var _this = this;
      // 1ID
      if (this.giveForId != '') {
        // ** 
        if (this.giveCoin <= 0) {
          _this.$message({
            message: '0',
            type: 'warning'
          });
          return
        }
        // ** 
        if ((this.giveCoin % 1) != 0) {
          _this.$message({
            message: '',
            type: 'warning'
          });
          return
        }

        // 2
        if (this.radio == 1) {
          // 3
          if (this.giveCoin > this.GFruit) {
            _this.$message({
              message: '',
              type: 'warning'
            });
          } else {
            this.$confirm('' + _this.giveCoin + '' + _this.giveForId + '', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.sendAjaxFun()
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          }
        } else {
          // 3
          if (this.giveCoin > this.GSeed) {
            _this.$message({
              message: '',
              type: 'warning'
            });
          } else {
            this.$confirm('' + _this.giveCoin + '' + _this.giveForId + '', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.sendAjaxFun()
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          }
        }
      } else {
        _this.$message({
          message: 'ID',
          type: 'warning'
        });
      }

    },

    //
    sendAjaxFun: function sendAjaxFun() {
      var _this = this;
      this.ajax(_this.port.sendGive, {
        username: _this.giveForId,
        give_coin: _this.giveCoin,
        coinType: _this.radio
      }, 'POST', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.$message({
            message: '',
            type: 'success'
          });
          if (_this.radio == 1) {
            _this.GFruit = _this.GFruit - _this.giveCoin;
          } else {
            _this.GSeed = _this.GSeed - _this.giveCoin;
          }
          _this.giveCoin = '';
          _this.giveForId = '';
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    this.getUserCoin();
  }
}
</script>
<style>
#giveToFriend .bodyMainRow {
  height: 1.1rem;
}

#giveToFriend .bodyMainHead {
  justify-content: space-around;
}

#giveToFriend .bodyMainHead p:last-child span {
  color: #00ac0c;
}

.radioBox {
  width: 3.3rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.radioBox .radioList {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 1.7rem;
}

.el-checkbox__input {
  width: 0.3rem;
  height: 0.4rem;
  margin-top: -0.15rem;
}
</style>



