<template>
  <div id="basics">
      <p class="centerRow">
          <span class="centerRowTit"></span>
          <span class="global_txtOver">{{Data.name}}</span>
      </p>
      <p class="global_orgBB"></p>
      <p class="centerRow">
          <span class="centerRowTit"></span>
          <span class="global_txtOver">{{Data.mobile}}</span>
      </p>
      <p class="global_orgBB"></p>
      <p class="centerRow">
          <span class="centerRowTit"></span>
          <span class="global_txtOver">{{Data.bank_account}}</span>
      </p>
      <p class="global_orgBB"></p>
      <p class="centerRow">
          <span class="centerRowTit"></span>
          <span class="global_txtOver">{{Data.deposit_bank}}</span>
      </p>
      <p class="global_orgBB"></p>
      <p class="centerRow">
          <span class="centerRowTit"></span>
          <span class="global_txtOver">{{Data.address}}</span>
      </p>
      <!--loading-->
      <div v-if="iSsuccess" class="bgblacks  in"></div>
      <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
      <!--loading-->
  </div>
</template>
<script>
export default {
  name: "basics",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      Data:{},

    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this
    this.ajax(_this.port.basicsDeta, {
    }, 'GET', function(res) {
      //  console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
    }else if (res.code == 0) {
          _this.$message.warning(res.message);
      }else{
        _this.$message({message: 'shit~!....',type: 'warning'});
      }
    })
  }
}
</script>
