-->
<template>
<div id="shareId">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>

  <div class="shareIdIptList global_boxSad">
    <div class="bodyMainRow">
      <span> </span>
      <div class="shareSureBox">
        <input type="text" name="" :value="thatUserId" @blur="suerthatUserId()" v-model="thatUserId" class="global_IptSad harvestRequestIpt" placeholder="">
        <div class="global_boxSad shareSureBtn" @click="suerTheId()">
          
        </div>
      </div>
    </div>
    <div class="bodyMainRow">
      <span> </span>

      <input type="text" name="" :value="nextUserId" v-model="nextUserId" @blur="suerTheNextId()" class="global_IptSad harvestRequestIpt" placeholder="5">

      <!-- <div class="shareSureBox">
        <input type="text" name="" :value="nextUserId" @blur="suernextUserId()" v-model="nextUserId" class="global_IptSad harvestRequestIpt" placeholder="">
        <div class="global_boxSad shareSureBtn" @click="suerTheNextId()">
          
        </div>
      </div> -->
    </div>
    <p class="bodyMainRow">
      <span></span>
      <input type="text" name="" :value="userName" v-model="userName" @blur="sureuserName()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span> </span>
      <input type="number" name="" :value="userPhone" v-model="userPhone" @blur="sureuserPhone()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span></span>
      <input type="password" name="" :value="userPsw1" v-model="userPsw1" @change="sureuserPsw1()" class="global_IptSad harvestRequestIpt" placeholder="6-15">
    </p>
    <p class="bodyMainRow">
      <span> </span>
      <input type="password" name="" :value="userPsw2" v-model="userPsw2" @blur="sureuserPsw2()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span>()</span>
      <input type="text" name="" :value="userBackA" v-model="userBackA" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span> ()</span>
      <input type="number" name="" :value="userBackNum" v-model="userBackNum" @blur="sureBackNum()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span>()</span>
      <input type="text" name="" :value="userAds" v-model="userAds" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span> </span>
      <input type="number" name="" :value="GFuIn" v-model="GFuIn" @blur="sureGFuIn()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <p class="bodyMainRow">
      <span></span>
      <input type="number" name="" :value="GSeedIn" v-model="GSeedIn" @blur="sureGSeedIn()" class="global_IptSad harvestRequestIpt" placeholder="">
    </p>
    <div class="bodyMainRow">
      <span></span>
      <div class="harvestRequestIpt" style="background-color:#fff5ee;display:flex;margin-right:0.5rem;">
        <span class="E_switch" style="width:1.5rem">
              <el-switch
                v-model="value1"
                on-text=""
                off-text="">
              </el-switch>
    </span>
        <!-- <div class="harvestRequestIpt" style="background-color:#fff5ee;
          display:flex;justify-content:flex-start;align-items:center;width:2rem;">
              
          </div> -->
        <el-button type="text" @click="dialogFormVisible = true"></el-button>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <div class="dealDeta">
            <h3></h3>
            <h4></h4>
            <h4></h4>
            <p>+N</p>
            <p>:</p>
            <p>
              
              <p>1900/</p>
              <p>2</p>
              <p>3</p>
            </p>
            <p>
              
              <p>1</p>
              <p>2</p>
              <p>3</p>
              <p>4</p>
            </p>
            <p>
               
              <p> :</p>
              <p>19003</p>
              <p>2</p>
              <p>3</p>
              <p>4:</p>
            </p>
            <p>
              
              <p>1100</p>
              <p>2</p>
              <p>3</p>
            </p>
            <p>:()</p>
            <p></p>
            <p></p>
            <p></p>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="dialogFormVisible = false; value1 = true"></el-button>
          </div>
        </el-dialog>



      </div>
    </div>
  </div>
  <div class="goTo110" @click="sureRegister()">
    
  </div>

  <div class="global_nullDiv">

  </div>

  <el-dialog title="ID" :visible.sync="remberIdBox">
    <div class="remberIdBox">
      <h3>:</h3>
      <div class="shareId-IdBox">
        {{okId}}
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="remberIdBox = false"></el-button>
    </div>
  </el-dialog>

  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "shareId",
  data: function data() {
    return {
      value1: false, //
      okId: '', //ID
      dialogFormVisible: false, // 
      remberIdBox: false, // ID
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex', //
      thatUserId: '', //Id
      userName: '', //
      userPhone: '', //
      userPsw1: '', //
      userPsw2: '', //
      userBackA: '', //
      userBackNum: '', //
      userAds: '', //
      GFuIn: '', //
      GSeedIn: '', //
      nextUserId: '', //

      thatUserIdFlag: false, //Id
      nextUserIdFlag: false, //Id
      userNameFlag: false, //
      userPhoneFlag: false, //
      userPsw1Flag: false, //
      userPsw2Flag: false, //
      GFuInFlag: false, //
      GSeedInFlag: false, //
      BackNumFlag: true,
    }
  },
  beforeRouteEnter(to, from, next) {
    // console.log(sessionStorage.getItem('accessToken'));
    let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next()
    }
  },
  methods: {
    //  ID
    suerTheId: function suerTheId() {
      var _this = this;
      this.ajax(_this.port.sureId, {
        username: _this.thatUserId
      }, 'GET', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.$alert('ID' + res.data.name, '', {
            confirmButtonText: '',
            callback: action => {}
          });
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
  }, //  
    suerTheNextId: function suerTheNextId() {
      var _this = this;
      this.ajax(_this.port.suerNextId, {
        username: _this.nextUserId
      }, 'GET', function(res) {
        // console.log(res);
        if (res.code == 1) {
            _this.$message.success(res.message);
            _this.nextUserIdFlag = true
        } else if (res.code == 0) {
            _this.$alert('5', '', {
              confirmButtonText: '',
              callback: action => {}
            });
            _this.nextUserIdFlag = false
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
          _this.nextUserIdFlag = false
        }
      })
    },
    //  
    sureBackNum: function sureBackNum() {
      var _this = this;
      if (this.userBackNum == '') {
        _this.BackNumFlag = true
      } else if (this.userBackNum % 1 != 0) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.BackNumFlag = false
      } else {
        _this.BackNumFlag = true
      }
    },
    //   
    suerthatUserId: function suerthatUserId() {
      var _this = this;
      if (this.thatUserId == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.thatUserIdFlag = false
      } else {
        _this.thatUserIdFlag = true
      }
    },

    //   
    suernextUserId: function suernextUserId() {
      var _this = this;
      if (this.nextUserId == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.nextUserIdFlag = false
      } else {
        _this.nextUserIdFlag = true
      }
    },
    //   
    sureuserName: function sureuserName() {
      var _this = this;
      if (this.userName == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userNameFlag = false
      } else if (this.userName.length > 4) {
        _this.$message({
          message: '4~',
          type: 'warning'
        });
        _this.userNameFlag = false
      } else {
        _this.userNameFlag = true
      }
    },
    //   
    sureuserPhone: function sureuserPhone() {
      var _this = this;
      let phoneNumber = this.userPhone
      if (this.userPhone == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userPhoneFlag = false
      } else if (!(/^1[34578]\d{9}$/.test(phoneNumber))) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userPhoneFlag = false
      } else {
        _this.userPhoneFlag = true
      }
    },
    //  1 
    sureuserPsw1: function sureuserPsw1() {
      var _this = this;
      if (this.userPsw1 == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userPsw1Flag = false
      } else if (this.userPsw1.length >= 6 && this.userPsw1.length < 15) {
        _this.userPsw1Flag = true
      } else {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userPsw1Flag = false
      }
    },
    //  2 
    sureuserPsw2: function sureuserPsw2() {
      var _this = this;
      if (this.userPsw2 != this.userPsw1) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.userPsw2Flag = false
      } else {
        _this.userPsw2Flag = true
      }
    },
    //   
    sureGFuIn: function sureGFuIn() {
      var _this = this;
      if (this.GFuIn > 500) {
        _this.$message({
          message: '500',
          type: 'warning'
        });
        _this.GFuInFlag = false;
        return;
      } else if (this.GFuIn == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GFuInFlag = false;
        return;
      } else if (this.GFuIn < 0) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GFuInFlag = false;
        return;
      } else if ((this.GFuIn % 1) != 0) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GFuInFlag = false;
        return;
      } else {
        _this.GFuInFlag = true
      }
    },
    //   
    sureGSeedIn: function sureGSeedIn() {
      var _this = this;
      if (this.GSeedIn < 400) {
        _this.$message({
          message: '400',
          type: 'warning'
        });
        _this.GSeedInFlag = false;
        return;
      } else if (this.GSeedIn == '') {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GSeedInFlag = false;
        return;
      } else if (this.GSeedIn < 0) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GSeedInFlag = false;
        return;
      } else if ((this.GSeedIn % 1) != 0) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        _this.GSeedInFlag = false;
        return;
      } else {
        _this.GSeedInFlag = true
      }
    },

    //
    sureRegister: function sureRegister() {
      var _this = this;
      if (!this.value1) {
        _this.$message({
          message: '~',
          type: 'warning'
        });
        return
      }
      //
      if (this.thatUserIdFlag && this.userNameFlag && this.userPhoneFlag && this.userPsw1Flag && this.userPsw2Flag && this.GFuInFlag && this.GSeedInFlag && this.value1 && this.nextUserIdFlag) {
        this.ajax(_this.port.register, {
          referrer_username: _this.thatUserId,
          username: _this.nextUserId,
          name: _this.userName,
          mobile: _this.userPhone,
          password: _this.userPsw1,
          re_password: _this.userPsw2,
          deposit_bank: _this.userBackA,
          bank_account: _this.userBackNum,
          address: _this.userAds,
          a_coin: _this.GFuIn,
          b_coin: _this.GSeedIn,

        }, 'POST', function(res) {
          // console.log(res);
          if (res.code == 1) {
            _this.remberIdBox = true;
            _this.okId = res.data.username;

            _this.value1 = false;
            _this.thatUserId = '';
            _this.nextUserId = '';
            _this.userName = '';
            _this.userPhone = '';
            _this.userPsw1 = '';
            _this.userPsw2 = '';
            _this.userBackA = '';
            _this.userBackNum = '';
            _this.userAds = '';
            _this.GFuIn = '';
            _this.GSeedIn = '';
            _this.thatUserIdFlag = false;
            _this.userNameFlag = false;
            _this.userPhoneFlag = false;
            _this.userPsw1Flag = false;
            _this.userPsw2Flag = false;
            _this.GFuInFlag = false;
            _this.GSeedInFlag = false;
            _this.BackNumFlag = true;


          } else if (res.code == 0) {
            _this.$message.warning(res.message);
          } else {
            _this.$message({
              message: 'shit~!....',
              type: 'warning'
            });
          }
        })
      } else {
        _this.$message({
          message: '',
          type: 'warning'
        });
      }
    }
  }
}
</script>
<style>
.el-switch__button {
  width: 16px !important;
  height: 16px !important;
}

#shareId .el-dialog--small {
  width: 90%
}

.dealDeta h3 {
  font-size: 0.36rem;
  text-align: center;
  margin-bottom: 0.1rem;
  color: #222;
}

.dealDeta h4 {
  font-size: 0.3rem;
}

.dealDeta p {
  color: #333;
  text-indent: 2em;
}

.remberIdBox h3 {
  font-size: 0.36rem;
  text-align: center;
  margin-bottom: 0.1rem;
  color: #222;
}

.remberIdBox .shareId-IdBox {
  font-size: 0.45rem;
  text-align: center;
  color: red;
}
</style>



