<!--
    =============  =============
-->
<template>
  <div id="statistics">
      <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>

      <!--  -->
      <div class="headTypeList">
        <div class="HeadTypeBox" :class=" allBtnFlag ? 'HeadTypeActive' : '' " @click="changeModule(1)">
          
        </div>
        <div class="HeadTypeBox" :class=" shareBtnFlag ? 'HeadTypeActive' : '' " @click="changeModule(2)">
            
        </div>
      </div>
      <!--  -->
      <div class="bodyMainBox">
        <transition name="el-zoom-in-center">
          <Sshare class="earnAllBox" v-if="allFlag"></Sshare>
        </transition>
        <transition name="el-zoom-in-center">
          <Sregister class="earnShareBox" v-if="shareFlag"></Sregister>
        </transition>
      </div>

      <!--loading-->
      <div v-if="iSsuccess" class="bgblacks  in"></div>
      <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
      <!--loading-->
  </div>
</template>
<script>
import Sregister from '@/components/statistics/S_register/S_register.vue'; //->
import Sshare from '@/components/statistics/S_share/S_share.vue'; //->
export default {
  name: "statistics",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex', //
      shareFlag: false, //
      allFlag: true, //
      performanceFlag: false, //
      shareBtnFlag: false, //
      allBtnFlag: true, //
      performanceBtnFlag: false, //
    }
  },
  beforeRouteEnter(to, from, next) {
      // console.log(sessionStorage.getItem('accessToken'));
      let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  }else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  } else {
      next()
    }
  },
  components: {
    'Sregister': Sregister,
    'Sshare': Sshare,
  },
  methods: {
    changeModule: function changeModule(typeNum) {
      var _this = this;
      var tempTimeOut
      if (typeNum == 1) {
        _this.shareFlag = false;
        _this.performanceFlag = false;

        _this.performanceBtnFlag = false;
        _this.shareBtnFlag = false;
        _this.allBtnFlag = true;
        tempTimeOut = setTimeout(function() {
          _this.allFlag = true;
      }, 300)
      } else if (typeNum == 2) {
        _this.allFlag = false;
        _this.performanceFlag = false;

        _this.allBtnFlag = false;
        _this.performanceBtnFlag = false;
        _this.shareBtnFlag = true;
        tempTimeOut = setTimeout(function() {
          _this.shareFlag = true;
      }, 300)
      } else {
        _this.shareFlag = false;
        _this.allFlag = false;

        _this.allBtnFlag = false;
        _this.shareBtnFlag = false;
        _this.performanceBtnFlag = true;
        tempTimeOut = setTimeout(function() {
          _this.performanceFlag = true;
      }, 300)
      }
    }
  }
}
</script>
<style>
</style>



