<!--
    =============  =============
-->
<template>
  <div id="basicsOverview" >
      <div class="basicsOverviewBox global_boxSad">
          <!--  -->
          <div class="bsOImgBox">
              <img src="../../assets/logo.jpg" alt="">
          </div>

          <!--  -->
          <div class="bsODetaBox">
              <div class="bsODetaName">
                  <div class="nameBox">
                      <h3 class="global_txtOver">{{Data.name}}</h3>
                      <p class="global_txtOver">:{{Data.username}} &nbsp; <span style="font-size:12px;" v-if="Data.parent_id != 0">:{{Data.parent_username}}</span></p>
                  </div>
                  <div class="btnBox">
                      <button type="button" name="button" class="global_orgBtn" @click="logout()"></button>
                  </div>
              </div>
              <div class="bsODetaViewBox">
                  <div class="bsOsunBox">
                      <h4></h4>
                      <span>{{Data.a_coin}}</span>
                  </div>
                  <div class="bsOsunBox">
                      <h4></h4>
                      <span>{{Data.b_coin}}</span>
                  </div>
                  <div class="bsOsunBox">
                      <h4></h4>
                      <span>{{Data.child_num}}</span>
                  </div>
              </div>
          </div>

      </div>
      <!--  -->
      <div class="centerMyTeam global_boxSad" v-if="teamFlag">
        <div class="centerTitBox">
          <div class="global_flexC">
            <span class="global_titLog"></span> 
          </div>
        </div>
        <div class="centerTitBox">
          <div class="centerTeamDeta">
            
            <span>{{Data.group_num}}</span>
          </div>
          <div class="centerTeamDeta">
            
            <span>{{Data.child}}</span>
          </div>
        </div>
      </div>
      <!--  -->
  </div>
</template>
<script>
export default {
  name: "basicsOverview",
  props: ['teamFlag'],
  data: function data() {
    return {
        Data:{},//
    }
},
mounted: function mounted() {
  //do something after mounting vue instance
  var _this = this
  this.ajax(_this.port.basicsDeta, {
  }, 'GET', function(res) {
    if (res.code == 1) {
      _this.Data = res.data;
  }else if (res.code == 0) {
        _this.$message.warning(res.message);
    }else{
      _this.$message({message: 'shit~!....',type: 'warning'});
    }
  })
},
methods: {
  logout: function logout() {
      var _this = this
      this.ajax(_this.port.logout, {
      }, 'POST', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.$message({
            message: '~',
            type: 'success'
          });
          sessionStorage.setItem("accessToken", 0);
          _this.$router.push('login')
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
  }
}
}
</script>
<style>
</style>



