<!--
    =============  =============
    
             
         
  
  
-->
<template>
<div id="begats">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>
  <!--  -->
  <div class="headRow" style="">
    <p>{{Data.username}}</p>
    <div class="begatsChangeBtn" @click="ajaxBegatsDeta(Data.username, true)">-</div>
  </div>
  <!--  -->
  <div class="branchImgBox">
    <img v-show="Data.num == 1" src="./1.png" alt="">
    <img v-show="Data.num == 2" src="./2.png" alt="" style="width:80%; margin:0 auto;display:block">
    <img v-show="Data.num == 3" src="./3.png" alt="">
  </div>
  <!--  -->
  <div class="begatsChildBox">
    <div class="headRow" style="" v-for="data in Data.child" :key="data.key">
      <p>{{data.username}}</p>
      <div class="begatsChangeBtn" @click="ajaxBegatsDeta(data.username, false)">+</div>
    </div>
  </div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "begats",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex', //
      Data: {},
      TopId: '',
      lastId: '', //Id
    }
  },
  beforeRouteEnter(to, from, next) {
    // console.log(sessionStorage.getItem('accessToken'));
    let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next()
    }
  },
  methods: {
      //
    ajaxBegatsDeta: function ajaxBegatsDeta(Id, upFlag) {
      let up = 0;
      var _this = this;
      // console.log(Id, this.TopId);
      //
      if (Id == this.TopId) {
        _this.$message({
          message: '',
          type: 'warning'
        });
        return
      }
      if (upFlag) {
        up = 1;
      }
      this.ajax(_this.port.begats, {
        username: Id, //ID
        up: up      //  10
      }, 'POST', function(res) {
        // console.log(res);
        if (res.code == 1) {
          _this.Data = res.data;
          _this.lastId = res.data.username;
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this;
    //ID
    this.ajax(_this.port.begats, {

    }, 'POST', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
        _this.TopId = res.data.username;
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
<style>

</style>



