<!--
    =============  =============
-->
<template>
<div id="myCommodity">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>
  <div v-if="hasT" class="">
    <img class="fruiterDetaPic" :src="Data.img_path" alt="">
    <p style="font-size:0.3rem;text-align:center;color:#222;margin-bottom:0.2rem;" v-if="!Data.img_path">
        
    </p>
    <div class="FDdetaBox" style="padding-bottom:0.18rem;">
      <div class="FDFruiterName">
        {{Data.fruiter_name}}
      </div>
      <div class="global_nullDiv">

      </div>
    </div>
  </div>
  <div v-if="!hasT" class="myCommodityRow">
    <p><img src="../../assets/shit.png" alt=""></p>
    <p></p>
  </div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "myCommodity",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex', //
      hasT: false, //
    }
  },
  beforeRouteEnter(to, from, next) {
      // console.log(sessionStorage.getItem('accessToken'));
      let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  }else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  } else {
      next()
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this;
    this.ajax(_this.port.myFruiter, {}, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.hasT = true
        _this.Data = res.data
      } else if (res.code == 0) {
        _this.hasT = false
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
<style>
</style>



