<!--
    =============  =============
-->
<template>
<div id="bannerDeta">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>
  <!--  -->
  <img :src="bannerImg" alt="" style="width:100%">
  <!--  -->
  <div class="bannerDetaBox">
      <p><img src="./bannerDetaLogo.png" alt=""></p>
      <h3>{{detaName}}</h3>
      <div id="bannerDetaDeta" class="bannerDetaDeta">

      </div>
  </div>

  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "bannerDeta",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex', //
      bannerImg:'', //banner
      detaName:'...',
    }
  },
  components: {},
  beforeRouteEnter(to, from, next) {
      // console.log(sessionStorage.getItem('accessToken'));
      let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  }else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  } else {
      next()
    }
  },
  created: function created() {
    //do something after creating vue instance
    var _this = this;
    var bannerId = _this.$route.params.bannerId;  //ID
    // console.log(bannerId);
    //banner
    this.ajax(_this.port.bannerDeta, {
        id: bannerId
    }, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
        _this.bannerImg = res.data.img;
        _this.detaName = res.data.name
        $('#bannerDetaDeta').html(res.data.content);
        // console.log(_this.bannerImg);
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
<style>

</style>



