<!--
    ============= > =============
-->
<template>
<div id="all">
  <p class="bodyMainHead">
    <!-- <i class="iconfont icon-ganxiejin"></i> -->
    <img src="./apple.png" alt="" style="width:0.4rem;height:0.4rem;padding-right:0.1rem;margin-top:-0.1rem;"> 
    <span>{{gross}}</span>
  </p>
  <div class="bodyMainRow">
    <span class="listTypeLon"></span>
    <span class="listTimeLon"></span>
    <span class="listOrtherLon" style="font-size:12px"></span>
    <span class="listOrtherLon" style="font-size:12px"></span>
  </div>
  <p class="global_orgBB"></p>
  <div class="bodyMainListBox" v-for="data in Data" :key="data.key">
    <div class="bodyMainRow">
      <span class="listTypeLon">{{data.typeName}}</span>
      <span class="listTimeLon">{{data.created_at}}</span>
      <span class="listOrtherLon global_txtOver">{{data.relate_username}}</span>
      <span class="listOrtherLon listOrtherLonDeta global_txtOver">{{data.num}}</span>
    </div>
    <p class="global_orgBB"></p>
  </div>
  <div class="global_nullDiv"></div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "all",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      Data: {},
      gross: '...'
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this;
    this.ajax(_this.port.myEarnings, {
      type: 0
    }, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data.data;
        _this.gross = res.data.gross;
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
<style>
</style>



