<!--
    =============  =============
-->
<template>
<div id="changeBasics">
  <p class="centerRow">
    <span class="centerRowTit"></span>
    <!-- <span class="global_txtOver">{{Data.name}}</span> -->
    <span class="global_txtOver">{{userName}}</span>
    <!-- <input type="text" name="" v-model="userName" :value="userName" class="global_IptSad"> -->
  </p>
  <p class="centerRow">
    <span class="centerRowTit"></span>
    <span class="global_txtOver">{{Data.mobile}}</span>
  </p>
  <p class="centerRow">
    <span class="centerRowTit"></span>
    <input type="text" name="" v-model="bankAccount" :value="bankAccount" class="global_IptSad">
  </p>
  <p class="centerRow">
    <span class="centerRowTit"></span>
    <input type="text" name="" v-model="depositBank" :value="depositBank" class="global_IptSad">
  </p>
  <p class="centerRow">
    <span class="centerRowTit"></span>
    <input type="text" name="" v-model="Address" :value="Address" class="global_IptSad">
  </p>
  <div class="passwordRow">
    <div class="sureChange passwordBtn" @click="sureChange()">
      
    </div>
  </div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "changeBasics",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      Data: {},
      userName: '...', //
      bankAccount: '...', //
      depositBank: '...', //
      Address: '...', //
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this
    this.ajax(_this.port.basicsDeta, {}, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
        _this.bankAccount = res.data.bank_account
        _this.depositBank = res.data.deposit_bank
        _this.Address = res.data.address
        _this.userName = res.data.name
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  },
  methods: {
    //   
    //   
    sureChange: function sureChange() {
      var _this = this
      if(_this.userName == ''){
          _this.$message({
            message: '~',
            type: 'warning'
          });
          return
      }
      this.ajax(_this.port.changeBasics, {
        name: _this.userName,
        bank_account: _this.bankAccount,
        deposit_bank: _this.depositBank,
        address: _this.Address,
    }, 'POST', function(res) {
        // console.log(res);
        if (res.code == 1) {
            // console.log('success');
            _this.$message({
              message: '~',
              type: 'success'
            });
        } else if (res.code == 0) {
          _this.$message.warning(res.message);
        } else {
          _this.$message({
            message: 'shit~!....',
            type: 'warning'
          });
        }
      })
    }
  }
}
</script>
<style>
</style>



