<!--
    =============  =============
    ~
-->
<template>
<div id="harvestRequest">
  <p class="bodyMainHead">
    
    <span>{{userCoin}}</span>
    <span class="harvestRequestWTit">
              ()
          </span>
  </p>
  <div class="harvestRequestIptBox global_boxSad">
    <p class="bodyMainRow">
      <span></span>
      <input type="number" name="" v-model="userIpt" :value="userIpt" class="global_IptSad harvestRequestIpt" placeholder="200">
    </p>
    <p class="HRwarningP">
      (10%)
    </p>
    <p class="HRwarningP">
      *
    </p>
  </div>
  <div class="goTo110" @click="requestGCoin()">
    
  </div>


    <!--loading-->
    <div v-if="iSsuccess" class="bgblacks  in"></div>
    <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
    <!--loading-->
</div>
</template>
<script>
export default {
  name: "harvestRequest",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      userIpt: '',
      userCoin: '....',
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this
    //
    this.ajax(_this.port.hasGCoin, {}, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.userCoin = res.data[0].a_coin;
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  },
  methods: {
    //
    requestGCoin: function requestGCoin() {
      var _this = this;
      if(_this.userIpt <= _this.userCoin){
          if (_this.userIpt % 1 == 0) {
              if(_this.userIpt <= 0 ){
                  _this.$message.warning('0~');
                  return
              }

              this.$confirm(''+_this.userIpt+'', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {

                _this.ajax(_this.port.requestGCoin, {
                  id: 2,
                  coin: _this.userIpt
                }, 'POST', function(res) {
                  // console.log(res);
                  if (res.code == 1) {
                      _this.$message({
                        message: '',
                        type: 'success'
                      });
                      _this.userCoin = _this.userCoin - _this.userIpt;
                      _this.userIpt = '';
                  } else if (res.code == 0) {
                    _this.$message.warning(res.message);
                  } else {
                    _this.$message({
                      message: 'shit~!....',
                      type: 'warning'
                    });
                  }
                })
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: ''
                });
              });

          } else {
            _this.$message.warning('200~');
          }
      }else{
          _this.$message.warning('~');
      }
    }
  }
}
</script>
<style>
#harvestRequest .bodyMainHead {
  display:-webkit-box;
display:-webkit-flex;
display:-ms-flexbox;
display:flex;
}

#harvestRequest .bodyMainRow {
  height: 1.1rem;
}

#harvestRequest .bodyMainRow span {
  margin-left: 0.8rem;
}

#harvestRequest .bodyMainRow input {
  margin-right: 0.8rem;
}

.bodyMainHead .harvestRequestWTit {
  display: inline-block;
  font-size: 0.20rem;
  width: auto;
  height: 0.27rem;
  background-color: #f01515;
  color: #fff;
  line-height: 0.27rem;
  text-align: center;
  padding: 0 0.07rem;
  border-radius: 0.03rem;
  margin-left: 0.2rem;
}

.harvestRequestIpt {
  width: 3.3rem;
  height: 0.6rem;
  border: none !important;
  border-radius: 0.06rem !important;
  padding-left: 0.2rem;
  background-color: rgba(233, 225, 220, 0.6);
}

.HRwarningP {
  font-size: 0.2rem;
  color: #f01515;
  text-align: center;
  padding-bottom: 0.2rem;
}

::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #fff;
}

:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
}

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
}

:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #fff;
}
</style>



