<!--
    =============  =============
-->
<template>
<div id="noticeDeta">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>

  <div class="noticeDetaMain global_boxSad">
    <div class="noticeDetaTitBox">
      <h2>
                <span class="global_titLog"></span>
                {{Data.title}}
            </h2>
      <div class="noticeTimeDeta">
        <span>{{Data.created_at}}</span>
        <span>{{Data.author}}</span>
      </div>
    </div>
    <div class="noticeDetaDeta" id="noticeDetaDetail">
      <!--    sd  -->
    </div>
  </div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "noticeDeta",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/noticeList', //
      Data:{},
      noticeMain: {},
    }
  },
  beforeRouteEnter(to, from, next) {
      // console.log(sessionStorage.getItem('accessToken'));
      let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  }else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
  } else {
      next()
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this
    var detaId = _this.$route.params.noticeDetaId;
    this.ajax(_this.port.noticeDeta, {
        id:detaId
    }, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
        $('#noticeDetaDetail').html(res.data.content);
        //_this.noticeMain =
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
<style>
.noticeDetaMain {
  background-color: #fff5ee;
  margin-top: 0.2rem;
}

.noticeDetaTitBox {
  border-bottom: solid 1px #ffddd0;
  padding: 0 0.2rem 0.07rem;
}

.noticeDetaTitBox h2 {
  height: 0.66rem;
  line-height: 0.66rem;
  font-size: 0.28rem;
  color: #222;
}

.noticeDetaDeta {
  font-size: 0.26rem;
  color: #666666;
  padding: 0.2rem 0.4rem 0.6rem;
  line-height: 1.5em
}
</style>



