<!--
    =============  =============
        ~ ~~~
    
    
    ~
-->
<template>
<div id="leaveMsg">
  <v-header :title="pageName" :btnFlag="backFlag" :backUrl="backUrl"></v-header>

  <div class="leaveMsgBox global_boxSad">
    <h3>1</h3>
    <p>wanankeji88</p>
    <img src="./88.png" alt="">
  </div>
  <div class="leaveMsgBox global_boxSad">
    <h3>2</h3>
    <p>wanankeji888</p>
    <img src="./888.png" alt="">
  </div>
  <div class="leaveMsgBox global_boxSad">
    <p>:</p>
    <p>09:12:00 14:0017:00</p>
    <p>:13008136276</p>
  </div>

  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "leaveMsg",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      pageName: '', //tiele
      backFlag: true, //FALSE   TRUE
      backUrl: '/mainIndex' //
    }
  },
  beforeRouteEnter(to, from, next) {
    // console.log(sessionStorage.getItem('accessToken'));
    let hasLogin = sessionStorage.getItem('accessToken')
    if (hasLogin == 0) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else if (hasLogin == null) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next()
    }
  },
}
</script>
<style>
.leaveMsgBox {
  width: 68%;
  margin: 0.2rem auto 0;
  text-align: center;
  background-color: #fff;
  border-radius: 0.2rem;
  padding: 0.1rem 0;
}

.leaveMsgBox h3 {
  font-size: 0.32rem;
}

.leaveMsgBox p {
  font-size: 0.26rem;
}

.leaveMsgBox img {
  width: 60%;
  margin: 0.2rem;
}
</style>



