<!--
    =============  =============
         
-->
<template>
<div id="depositRecord">
  <div class="bodyMainRow">
    <span class="listTypeLon"></span>
    <span class="listTimeLon"></span>
    <span class="listOrtherLon"></span>
  </div>
  <p class="global_orgBB"></p>
  <div class="bodyMainListBox" v-for="data in Data" :key="data.key">
    <div class="bodyMainRow">
      <span class="listTypeLon global_txtOver">{{data.total}}</span>
      <span class="listTimeLon">{{data.created_at}}</span>
      <span class="listOrtherLon global_txtOver depositRWait" v-if="data.status == 0"></span>
      <span class="listOrtherLon global_txtOver depositRSucceed" v-else-if="data.status == 1"></span>
      <span class="listOrtherLon global_txtOver depositRDefeated" v-else-if="data.status == 2"></span>
    </div>
    <p class="global_orgBB"></p>
  </div>
  <div class="global_nullDiv"></div>

  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "depositRecord",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      Data: {}, //
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this
    this.ajax(_this.port.hervestList, {
      id: 2
    }, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data;
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  },
}
</script>
<style>
.depositRWait {
  color: #f84a05;
}

.depositRSucceed {
  color: #00ac0c;
}

.depositRDefeated {
  color: #fe1818;
}
</style>



