import Vue from 'vue';
import Router from 'vue-router';
//import Hello from '@/components/Hello';

import mainIndex from '@/components/mainIndex/mainIndex.vue'; //
import bannerDeta from '@/components/bannerDeta/bannerDeta.vue'; //
import basics from '@/components/center/basics/basics.vue'; //

import center from '@/components/center/center.vue'; //
import myCommodity from '@/components/myCommodity/myCommodity.vue' //
import noticeList from '@/components/noticeList/noticeList.vue' //
import allCommodity from '@/components/allCommodity/allCommodity.vue' //
import fruiterDeta from '@/components/fruiterDeta/fruiterDeta.vue' //

import earnings from '@/components/earnings/earnings.vue' //
import begats from '@/components/begats/begats.vue' // 
import begats2 from '@/components/begats2/begats2.vue' // 

import give from '@/components/give/give.vue' //
import harvest from '@/components/harvest/harvest.vue' //
import shareId from '@/components/shareId/shareId.vue' //
import wallet from '@/components/wallet/wallet.vue' //
import noticeDeta from '@/components/noticeDeta/noticeDeta.vue' //


import changeBasics from '@/components/center/changeBasics/changeBasics.vue'; //
import password from '@/components/center/password/password.vue'; //
import basicsOverview from '@/components/basicsOverview/basicsOverview.vue'; //

import all from '@/components/earnings/all/all.vue'; //->
import share from '@/components/earnings/share/share.vue'; //->
import performanc from '@/components/earnings/performance/performance.vue'; //->
import topup from '@/components/earnings/topup/topup.vue'; //->

import harvestRequest from '@/components/harvest/harvestRequest/harvestRequest.vue'; //->
import depositRecord from '@/components/harvest/depositRecord/depositRecord.vue'; //->

import giveToFriend from '@/components/give/giveToFriend/giveToFriend.vue'; //->
import giveRecord from '@/components/give/giveRecord/giveRecord.vue'; //->
import getRecord from '@/components/give/getRecord/getRecord.vue'; //->



import login from '@/components/login/login.vue' //
import adminTree from '@/components/adminTree/adminTree.vue' //


import statistics from '@/components/statistics/statistics.vue' //
import S_register from '@/components/statistics/S_register/S_register.vue' //
import S_share from '@/components/statistics/S_share/S_share.vue' //

import leaveMsg from '@/components/leaveMsg/leaveMsg.vue' //
import account from '@/components/account/account.vue' //

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/mainIndex',
      name: 'mainIndex',
      component: mainIndex
    },
    {
      path: '/center',
      name: 'center',
      component: center,
      children: [{
          path: '/center/basics/basics.vue',
          component: basics
        },
        {
          path: '/center/changeBasics/changeBasics.vue',
          component: changeBasics
        },
        {
          path: '/center/password/password.vue',
          component: password
        },
      ],
    }, {
      path: '/basicsOverview',
      name: 'basicsOverview',
      component: basicsOverview
    }, {
      path: '/login',
      name: 'login',
      component: login
    }, {
      path: '/bannerDeta',
      name: 'bannerDeta',
      component: bannerDeta
    }, {
      path: '/myCommodity',
      name: 'myCommodity',
      component: myCommodity
    }, {
      path: '/noticeList',
      name: 'noticeList',
      component: noticeList
    }, {
      path: '/allCommodity',
      name: 'allCommodity',
      component: allCommodity
    }, {
      path: '/earnings',
      name: 'earnings',
      component: earnings,
      children: [{
          path: '/earnings/all/all.vue',
          component: all
        },
        {
          path: '/earnings/share/share.vue',
          component: share
        },
        {
          path: '/earnings/performance/performance.vue',
          component: performanc
        },
        {
          path: '/earnings/topup/topup.vue',
          component: topup
        },
      ],
    }, {
      path: '/begats',
      name: 'begats',
      component: begats
    }, {
      path: '/begats2',
      name: 'begats2',
      component: begats2
    }, {
      path: '/give',
      name: 'give',
      component: give,
      children: [{
          path: '/give/giveToFriend/giveToFriend.vue',
          component: harvestRequest
        },
        {
          path: '/give/giveRecord/giveRecord.vue',
          component: giveRecord
        },
        {
          path: '/give/getRecord/getRecord.vue',
          component: getRecord
        },
      ],
    }, {
      path: '/harvest',
      name: 'harvest',
      component: harvest,
      children: [{
          path: '/harvest/harvestRequest/harvestRequest.vue',
          component: harvestRequest
        },
        {
          path: '/harvest/depositRecord/depositRecord.vue',
          component: depositRecord
        },
      ],
    }, {
      path: '/shareId',
      name: 'shareId',
      component: shareId
    }, {
      path: '/wallet',
      name: 'wallet',
      component: wallet
    }, {
      path: '/fruiterDeta',
      name: 'fruiterDeta',
      component: fruiterDeta
    }, {
      path: '/noticeDeta',
      name: 'noticeDeta',
      component: noticeDeta
    }, {
      path: '/adminTree',
      name: 'adminTree',
      component: adminTree
    }, {
      path: '/statistics',
      name: 'statistics',
      component: statistics,
      children: [{
          path: '/statistics/S_register/S_register.vue',
          component: S_register
        },
        {
          path: '/statistics/S_share/S_share.vue',
          component: S_share
        },
      ],
    }, {
      path: '/leaveMsg',
      name: 'leaveMsg',
      component: leaveMsg
    }, {
      path: '/account',
      name: 'account',
      component: account
    },
    {
      path: '*',
      redirect: '/mainIndex'
    } //
  ],

})



