<template>
<div id="giveRecord">
  <div class="bodyMainRow">
    <span class="listTypeLon"></span>
    <span class="listTimeLon"></span>
    <span class="listOrtherLon"></span>
    <span class="listOrtherLon"></span>
  </div>
  <p class="global_orgBB"></p>
  <div class="bodyMainListBox" v-for="data in Data" :key="data.key">
    <div class="bodyMainRow">
      <span class="listTypeLon global_txtOver">{{data.member_username}}</span>
      <span class="listTimeLon">{{data.created_at}}</span>
      <span class="listOrtherLon global_txtOver depositRWait" v-if="data.type == 1"></span>
      <span class="listOrtherLon global_txtOver depositRSucceed" v-else-if="data.type == 2"></span>
      <span class="listOrtherLon global_txtOver depositRSucceed" v-else style="color:#ccc"></span>
      <span class="listOrtherLon listOrtherLonDeta global_txtOver">{{data.give_coin}}</span>
    </div>
    <p class="global_orgBB"></p>
  </div>


  <div class="global_nullDiv"></div>
  <!--loading-->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "giveRecord",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      Data: {},
    }
  },
  methods: {
    fun: function fun() {

    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    var _this = this;
    this.ajax(_this.port.getRecord, {}, 'GET', function(res) {
      // console.log(res);
      if (res.code == 1) {
        _this.Data = res.data
      } else if (res.code == 0) {
        _this.$message.warning(res.message);
      } else {
        _this.$message({
          message: 'shit~!....',
          type: 'warning'
        });
      }
    })
  }
}
</script>
